@use 'variables' as v;

h1, h2, h3 {
    font-family: v.$fuente_principal;
    margin: 0 0 calc(v.$separacion / 2) 0 ;
    font-weight: 900;
    color: v.$blanco;
    text-align: center;
}

h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
    text-align: center;
}
p {
    color: v.$negro;
    margin: 0px !important;
}
label{
    font-size:13px
}
input{
    font-size: 20px;
    height: 30px;
}
input[type="checkbox"]{
    width:20px;
    height:20px;

}